import React, { PureComponent, Suspense } from "react";
import { connect } from 'react-redux';
import { filterPostDataAppendWebFilter } from '../../actions/themeActions'
import "./scss/ReviewHeader.scss"
import AverageRating from "../Themes/Elements/AverageRating/AverageRating";
import DefaultNetwork from "../Themes/Elements/Network/DefaultNetwork";

class ReviewHeader extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      networkID: null,
      isOpen: false,
      activeId: 0,
      network: {
        name: '',
        avgRate: 0,
        totalReviews: 0,
        color: this.props.wall.Banner.title_font_color
      }
    };
    this.dropdownRef = React.createRef();
  }
  toggleDropdown = () => {
    const { activeId } = this.state
    const { reviewFilters } = this.props
    if (activeId != 0 && reviewFilters.length > 0) {
      const redirectLink = reviewFilters.filter((item) => item.Network.id == activeId);
      if (redirectLink && redirectLink.length > 0) window.open(redirectLink[0].Network.link, "_blank")
    }
    else this.setState(prevState => ({
      isOpen: !this.state.isOpen
    }));
  }
  componentDidMount() {
    document.addEventListener('click', this.handleClickOutside);
    const { network, activeId } = this.onLoadTotalRating(this.props.reviewFilters)
    this.setState({ network: network, activeId: activeId, networkID: activeId })
  }
  componentWillUnmount() {
    document.removeEventListener('click', this.handleClickOutside);
  }
  handleClickOutside = (event) => {
    if (this.dropdownRef.current && !this.dropdownRef.current.contains(event.target)) {
      this.setState(prevState => ({
        isOpen: false
      }));
    }
  }
  onLoadTotalRating = (reviewFilters) => {
    let avgReview = 0
    let totalReview = 0
    let networkName = ''
    let activeId = 0
    let color = this.props.wall.Banner.title_font_color

    reviewFilters.map((items) => {
      const item = items.Network
      avgReview += item.averageReviews * item.totalReviews;
      totalReview += item.totalReviews;
      networkName = reviewFilters.length > 1 ? 'Overall' : item.name
      activeId = reviewFilters.length > 1 ? 0 : item.id
      color = reviewFilters.length > 1 ? this.props.wall.Banner.title_font_color : item.color
    })
    return {
      network: {
        name: networkName,
        avgRate: isNaN(avgReview / totalReview) ? 0 : (avgReview / totalReview).toFixed(1),
        totalReviews: totalReview,
        color: color
      },
      activeId: activeId
    }
  }

  totalReviewNetworks = (reviewFilters) => event => {
    const { network, activeId } = this.onLoadTotalRating(reviewFilters)
    if (this.props.changeFilterStatus) this.props.changeFilterStatus(2)

    const tstamp = Math.floor(Date.now() / 1000);
    const { appendData, wall } = this.props;
    let postCount = wall.ThemeRule.numberOfPosts;
    this.props.filterPostDataAppendWebFilter(
      wall.Wall.id,
      tstamp,
      postCount,
      activeId,
      appendData.after,
      appendData.heightEvent,
      null
    );
    this.setState({
      network: network,
      activeId: activeId,
      networkID: activeId
    })
  }



  render() {
    const { reviewFilters, wall, languageSetting } = this.props;
    const { isOpen, network, activeId } = this.state;

    const subTitle = { color: wall.Banner.subTitle_font_color, fontFamily:wall.Banner.subtitle_css_font, fontSize:wall.Banner.subTitle_font_size }
    return (
      <div className="tb_rv_container" style={{ padding: wall.Personalization.padding / 2 }}>
        <div className="tb_rv_wrapper" style={{ borderRadius: wall.ThemeRule.radius, backgroundColor:wall.Banner.background_color }}>
          {reviewFilters && reviewFilters.length > 1 ? <div className="tb_rv_scrollable">
            <div className="tb_rv_filter_wrap">
              <div className="tb_rv_filter_wrap_in">
                {Object.keys(reviewFilters).length > 0 ?
                  <div className={`tb_fil_soc_btn${activeId == 0 ? ' tb_fil_active' : ''}`} onClick={this.totalReviewNetworks(reviewFilters)}>
                    <div className="tb_fil_btn_txt">{languageSetting.filterButton}</div>
                  </div>
                  : null}
                {Object.keys(reviewFilters).length > 0 && reviewFilters.map((items, index) => {
                  const item = items.Network;
                  return <div key={index} className={`tb_fil_soc_btn${activeId == item.id ? ' tb_fil_active' : ''}`} onClick={this.totalReviewNetworks([items])}>
                    <DefaultNetwork network={item} networkClass={'tb_review_filter_ico'} />
                  </div>
                })
                }
              </div>
            </div>
          </div> : null}
          <div className="tb_rv_banner_wrap" style={{ minHeight: wall.Banner.banner_height }}>
            <div className="tb_rv_rating">
              <div className={`tb_rv_head_text tb-cTBfont-${wall.Banner.title_font_varient}`} style={{ color: wall.Banner.title_font_color, fontFamily:wall.Banner.title_css_font, fontSize:wall.Banner.title_font_size }}>
                <div className="tb_rv_head_social_text" style={{ color: network.color }}>{network.name}</div> Rating
              </div>
              <div className="tb_rv_rating_wrap">
                <div className="tb_rv_review_count" style={{color: wall.Banner.subTitle_font_color}}>{network.avgRate}</div>
                <div className="tb_rv_rview_star">
                  <AverageRating size={false} rating={network.avgRate} />
                </div>
                <div className={`tb_rv_review_total_post tb-cTBfont-${wall.Banner.subtitle_font_varient}`} style={subTitle}>{network.totalReviews} reviews</div>
              </div>
            </div>
            <div className="tb_rv_banner_btn_wrap" ref={this.dropdownRef}>
              <div className="tb_rv_banner_btn" onClick={this.toggleDropdown} style={{ backgroundColor: wall.Banner.background_color_two, color: '#fff' }}>Write a Review</div>
              {isOpen && (
                <div className={`tb_rv_dropdown_ ${isOpen ? 'tb_rv_dd_show' : ''}`}>
                  <div className="tb_rv_dropdown_title">Where do you want to leave a review?</div>
                  {Object.keys(reviewFilters).length > 0 && reviewFilters.map((items, index) => {
                    const item = items.Network;
                    return <div key={index} className="tb_rv_dropdown_list" onClick={event => item.link ? window.open(item.link, "_blank") : null}>
                      <DefaultNetwork network={item} networkClass={'tb_rv_dd_ico'} />
                      {item.name}
                    </div>
                  })
                  }
                </div>)}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
  }
}
const mapDispatchToProps = dispatch => {
  return {
    filterPostDataAppendWebFilter: (wallID, timeStamp, postCount, networkId, after, heightEvent, tagName) => dispatch(filterPostDataAppendWebFilter(wallID, timeStamp, postCount, networkId, after, heightEvent, tagName))
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(ReviewHeader);